.app {
    display: flex;
  }
  
  canvas {
    position: absolute;
  }
  
  .navbar {
    width: 100%;
    height: 50px;
    background-color: #4064ac;
    display: flex;
    align-items: center;
    color: white;
    font-weight: bold;
    padding: 0px 100px;
    font-size: 20px;
  }
  
  .newPostCard {
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  
  .addPost {
    display: flex;
  }
  
  label {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  
  .addImg {
    width: 25px;
    cursor: pointer;
  }
  
  .postForm {
    display: flex;
    flex-direction: column;
  }
  
  .avatar {
    width: 50px;
    height: 50px;
    border-radius: 50%;
    margin-right: 20px;
  }
  
  button {
    border: none;
    padding: 5px 10px;
    border-radius: 5px;
    color: white;
    background-color: teal;
    cursor: pointer;
  }
  
  .postInput {
    height: 30px;
    border: none;
    margin-bottom: 10px;
  }
  
  .container {
    margin: auto;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
  }
  
  .left {
    flex:6;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
  }
  
  .right {
    flex:6;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-weight: 300;
    
  }
  
  .rightInput {
    padding: 20px;
    margin-bottom: 20px;
  }
  
  .rightButton {
    padding: 10px 20px;
    font-weight: bold;
  }
  
  .friendInput {
    position: absolute;
    padding: 10px;
    font-weight: bold;
    border: none;
  }
  
  .friends {
    font-size: 20px;
    margin-bottom: 20px;
  }
  
  .name {
    font-weight: bold;
  }
  .image{
    height: 400px;
    width:400;
    object-fit: cover;
  
  }
  @media (max-width:700px){
    .containe{
      height: auto;
      flex:1 1;
  
    }
    .image{
      height: 100vh;
      width: 100wh;
      object-fit: cover;
    }
  }
